      <van-circle v-model="currentRate" :rate="80" :speed="100" :text="text"  style="margin-top: 1rem;"/>
      <van-cell title="运动步数" value="步数记录" @click="clickD" is-link/>
      <van-cell title="跑步记录" value="详情信息" is-link/>
      <van-cell title="健康指数"  value="健康指数"  is-link/>
      <van-cell title="有氧运动"  value="运动时间"  is-link/>
      <van-cell title="健身打卡"  value="打卡日历"  is-link/>
