  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data() {
    return {
      currentRate: 0,
      showdata:false,
      showbotton:true,
      showShare: false,
      options: [
        { name: 'Wechat', icon: 'wechat' },
        { name: 'Web', icon: 'link' },
        { name: 'Post', icon: 'poster' },
        { name: 'QRcode', icon: 'qrcode' },
      ],
    };
  },
  methods: {
    clickD(){
      this.showdata = true;
      this.showbotton=false
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
  },
  computed: {
    text() {
      return "运动分："+this.currentRate.toFixed(0);
    },
  },
};
</script>

<style>
.main .van-cell{
